.reviews {
  margin-top: 248px;
  &__title {
    margin: 0;
    margin-bottom: 43px;
  }
  &__swiper {
    position: relative;
    overflow: visible;
  }
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include mobile {
      flex-direction: column;
    }
  }
  &__item {
    &--picture {
      position: relative;
      margin-left: auto;
      &::before {
        content: '';
        position: absolute;
        right: -5px;
        top: -5px;
        z-index: -1;
        width: 20px;
        height: 20px;
        @include bg-cover;
        background-image: url('../img/reviews-effect.png');
      }
      &::after {
        content: '';
        position: absolute;
        left: -5px;
        bottom: -5px;
        z-index: 10;
        width: 20px;
        height: 20px;
        @include bg-cover;
        background-image: url('../img/reviews-effect2.png');
      }
    }
  }
  &__descr {
    margin: 0;
    margin-top: 30px;
    margin-bottom: 40px;
    max-width: 450px;
    font-family: 'Mulish';
    font-weight: 400;
    font-size: 24px;
    line-height: 36px;
    color: var(--black-color);
    @include small-tablet {
      font-size: 16px;
      margin-right: 15px;
    }
  }
  &__name {
    margin: 0;
    font-weight: 700;
    font-size: 22px;
    line-height: 48px;
    color: var(--black-color);
  }
  &__prof {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 48px;
    color: rgba(0, 0, 0, 0.85);
  }
  &__btns {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 100;
    background-color: var(--light-color);
    width: 132px;
    height: 60px;
    border-radius: 16px 0px 0px 0px;
  }
  &__slider-btn {
    position: absolute;
    width: 40px;
    height: 40px;
    z-index: 100;
    transition: all 0.3s;
    &--prev {
      left: 10px;
      bottom: 10px;
      @include bg-cover;
      background-image: url('../img/arrow-prev.svg');

    }
    &--next {
      right: 10px;
      bottom: 10px;
      @include bg-cover;
      background-image: url('../img/arrow-next.svg');
    }
  }
}

.swiper-button-disabled {
  opacity: 0.5;
}

