.featured {
  position: relative;
  margin-top: 262px;
  &::after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: -39px;
    z-index: -1;
    transform: translateX(-50%);
    width: 20px;
    height: 20px;
    background-image: url('../img/featured-effect3.png');
  }
  &__top {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 56px;
    @include mobile {
      flex-direction: column;
    }
    &::after {
      content: '';
      position: absolute;
      right: 171px;
      top: 0;
      z-index: -1;
      width: 20px;
      height: 20px;
      @include bg-cover;
      background-image: url('../img/featured-effect.png');
    }
  }
  &__title {
    margin: 0;
  }
  &__link {
    margin-right: 15px;
    position: relative;
    font-weight: 600;
    font-size: 17px;
    line-height: 60px;
    color: var(--btn-color);
    &::after{
      content: '';
      position: absolute;
      top: 50%;
      right: -15px;
      transform: translateY(-50%);
      width: 4px;
      height: 9px;
      @include bg-cover;
      background-image: url('../img/featured-arrow.svg');
      transition: all 0.3s;
    }
    &:hover::after {
      right: -20px;
    }
  }
  &__content {
    position: relative;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 25px;

    &::before {
      content: '';
      position: absolute;
      left: -96px;
      bottom: 115px;
      z-index: -1;
      width: 20px;
      height: 20px;
      @include bg-cover;
      background-image: url('../img/featured-effect2.png');
    }
  }
  &__card {
    position: relative;
    grid-column: 3 span;

    @include tablet {
      grid-column: 4 span;
    }
    @include small-tablet {
      grid-column: 6 span;
    }
    @include mobile {
      grid-column: 12 span;
    }
    &-item {
      position: absolute;
      bottom: 0;
      left: 0;
    }
    &-title {
      margin: 0;
      font-weight: 600;
      font-size: 17px;
      line-height: 25px;
      color: #042521;
    }
    &-descr {
      margin: 0;
      font-weight: 500;
      font-size: 15px;
      line-height: 24px;
      color: #80918E;
    }
  }

}
