.intro {
  margin-top: 72px;
  &__inner {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
  &__title {
    margin: 0;
    margin-bottom: 74px;
    position: relative;
    max-width: 356px;
    font-family: 'Playfair Display';
    font-weight: 700;
    font-size: 64px;
    line-height: 80px;
    color: var(--dark-color);
    @include tablet {
      font-size: 48px;
    }
    &::after {
      content: '';
      position: absolute;
      right: -112px;
      top: 0;
      width: 20px;
      height: 20px;
      @include bg-cover;
      background-image: url('../img/intro-effect.png');
    }
  }
  &__image {
    @include small-tablet {
      display: none;
    }
  }
  &__subtitle {
    position: relative;
    margin: 0;
    margin-bottom: 71px;
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;
    color: var(--dark-color);
    &::before {
      content: '';
      position: absolute;
      bottom: -21px;
      left: 0;
      width: 32px;
      height: 2px;
      background-color: var(--dark-color);
    }
    &::after {
      content: '';
      position: absolute;
      bottom: -33px;
      left: -85px;
      width: 26px;
      height: 26px;
      @include bg-cover;
      background-image: url('../img/intro-effect2.png');
    }
  }
  &__selects {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 30px;
    row-gap: 35px;
    margin-bottom: 47px;
    @include big-tablet {
      gap: 15px ;
    }
  }
  &__select {
    position: relative;
    grid-column: 6 span;
    padding: 21px 129px 21px 31px;
    border: 1px solid #AFB0B9;
    border-radius: 6px;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    background: url('../img/arrow-down.svg') no-repeat;
    background-position: right 30px  center;
    @include big-tablet {
      grid-column: 12 span;
    }
    @include small-tablet {
      grid-column: 6 span;
    }
    @include mobile {
      grid-column: 12 span;
    }
  }
}


