.guides {
  margin-top: 232px;
  &__inner {
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    justify-content: space-between;
    position: relative;
    @include small-tablet {
      flex-direction: column;
    }
    &::after {
      content: '';
      position: absolute;
      right: -58px;
      top: 44px;
      width: 20px;
      height: 20px;
      @include bg-cover;
      background-image: url('../img/guides-effect2.png');
    }
  }
  &__content {
    position: relative;
    max-width: 481px;
    &::after {
      content: '';
      position: absolute;
      right: 39px;
      bottom: 9px;
      width: 20px;
      height: 20px;
      @include bg-cover;
      background-image: url('../img/guides-effect2.png');
    }
  }
  &__title {
    position: relative;
    margin: 0;
    margin-bottom: 20px;
    &::before {
      content: '';
      position: absolute;
      top: -47px;
      left: 42px;
      width: 20px;
      height: 20px;
      @include bg-cover;
      background-image: url('../img/guides-effect.png');
    }
  }
  &__descr {
    margin: 0;
    margin-bottom: 39px;
  }
}
