.header {
  padding: 35px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__right {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include small-tablet {
      display: none;
    }
  }
  &__button {
    margin-left: 18px;
    @include small-tablet {
      margin-top: 10px;
    }
  }
}

.nav {
  @include small-tablet {
    margin-top: 20px;
  }
  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include small-tablet {
      flex-direction: column;
      align-items: flex-start;
    }
  }
  &__item {
    &:not(:last-child) {
      margin-right: 34px;
    }
  }
  &__link {
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #B8BECD;
    transition: all 0.3s;
    @include small-tablet {
      font-size: 24px;
    }
    &:hover {
      color: var(--dark-color);
      text-shadow: 0.1px 0 var(--dark-color);
    }
    &--active {
      font-weight: 500;
      color: var(--dark-color);
    }
  }
}

.burger {
  display: none;
  @include small-tablet {
    display: block;
  }
}

@include small-tablet {
  .burger { @include burger }
}

.menu--active {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100vh;
  background-color: var(--light-color);
}
