.trending {
  margin-top: 213px;
  margin-bottom: 290px;
  &__inner {
    position: relative;
    &::after {
      content: '';
      position: absolute;
      bottom: -200px;
      right: 365px;
      width: 20px;
      height: 20px;
      @include bg-cover;
      background-image: url('../img/trending-effect2.png');
    }
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    @include mobile {
      flex-direction: column;
    }
    &::before {
      content: '';
      position: absolute;
      top: -34px;
      left: 402px;
      height: 20px;
      width: 20px;
      @include bg-cover;
      background-image: url('../img/trending-effect.png');
    }
  }
  &__link {
    margin-right: 15px;
    position: relative;
    font-weight: 600;
    font-size: 17px;
    line-height: 60px;
    color: var(--btn-color);
    &::after{
      content: '';
      position: absolute;
      top: 50%;
      right: -15px;
      transform: translateY(-50%);
      width: 4px;
      height: 9px;
      @include bg-cover;
      background-image: url('../img/featured-arrow.svg');
      transition: all 0.3s;
    }
    &:hover::after {
      right: -20px;
    }
  }
  &__bottom {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    position: relative;
    gap: 21px;
    @include big-tablet {
      gap: 15px;
    }
    @include tablet {
      row-gap: 200px;
    }
    &::before {
      content: '';
      position: absolute;
      left: -74px;
      bottom: 0;
      width: 20px;
      height: 20px;
      @include bg-cover;
      background-image: url('../img/trending-effect2.png');
    }
  }
  &__item {
    position: relative;
    grid-column: 3 span;
    @include tablet {
      grid-column: 4 span;
    }
    @include small-tablet {
      grid-column: 6 span;
    }
    @include mobile {
      grid-column: 12 span;
    }
    &-content {
      position: absolute;
      bottom: -100px;
      left: 0;
      max-width: 266px;
      @include big-tablet {
        bottom: -150px;
      }
    }
    &-title {
      margin: 0;
      margin-bottom: 13px;
      font-weight: 700;
      font-size: 19px;
      line-height: 30px;
      color: var(--black-color);
      @include big-tablet {
        font-size: 17px;
      }
    }
    &-descr {
      margin: 0;
      margin-bottom: 23px;
      font-weight: 400;
      font-size: 15px;
      line-height: 26px;
      color: var(--grey-color);
    }
    &-link {
      position: relative;
      font-weight: 600;
      font-size: 15px;
      line-height: 28px;
      color: var(--btn-color);
      &::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 0;
        height: 1px;
        background-color: var(--btn-color);
        transition: width 0.3s ease-in-out;
      }
      &:hover::after {
        width: 100%;
      }
    }
  }
}
