.footer {
  padding-top: 78px;
  background-color: #F9F9FB;
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 77px;
    margin-bottom: 30px;
    border-bottom: 1px solid #C4C4C4;
    @include tablet {
      flex-direction: column;
      align-items: flex-start;
    }
  }
  &__column {
    display: flex;
    flex-direction: column;
  }
  &__descr {
    margin: 0;
    margin-top: 26px;
    margin-bottom: 55px;
    max-width: 391px;
    font-family: 'Mulish';
    font-weight: 300;
    font-size: 18px;
    line-height: 28px;
    color: #848484;
  }
  &__copyright {
    font-family: 'Mulish';
    font-weight: 300;
    font-size: 17px;
    line-height: 28px;
    color: #848484;
    transition: all 0.3s;
    &:hover {
      text-shadow: 0.1px 0 var(--dark-color),  0.1px 0 var(--dark-color),  0.1px 0 var(--dark-color);
      color: var(--black-color);
    }
  }
  &__title {
    margin: 0;
    font-family: 'Mulish';
    font-weight: 700;
    font-size: 19px;
    line-height: 60px;
    color: var(--black-color);
  }
  &__link {
    font-family: 'Mulish';
    font-weight: 300;
    font-size: 18px;
    line-height: 38px;
    color: #848484;
    transition: color 0.3s linear;
    &:hover {
      color: var(--black-color);
    }
  }
}

.social {
  margin-bottom: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  &__item {
    &:not(:last-child) {
      margin-right: 35px;
    }
  }
  &__icon {
    width: 25px;
    height: 20px;
    transition: all 0.3s;
    &:hover {
      opacity: 0.5;
    }
  }
}
