.btn {
  background-color: var(--btn-color);
  padding: 0 36px;
  font-weight: 500;
  font-size: 15px;
  line-height: 60px;
  color: var(--light-color);
  border-radius: 8px;
  transition: all 0.3s;

  &:hover {
    background-color: rgb(255, 102, 0);
  }
  &--login {
    padding: 0 46px;
    background-color: transparent;
    border: 1px solid var(--btn-color);
    color: var(--btn-color);
    transition: all 0.3s;

    &:hover {
      background-color: var(--btn-color);
      color: var(--light-color);
    }
  }
}

.title {
  font-family: 'Playfair Display';
  font-weight: 700;
  font-size: 36px;
  line-height: 50px;
  color: var(--black-color);
  @include tablet {
    font-size: 30px;
  }
}

.descr {
  font-family: 'Mulish';
  font-weight: 300;
  font-size: 17px;
  line-height: 30px;
  color: var(--grey-color);

}
