.explore {
  margin-top: 270px;
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include small-tablet {
      flex-direction: column;
    }
  }
  &__content {
    max-width: 481px;
    margin-left: 15px;
  }
  &__title {
    position: relative;
    margin: 0;
    margin-bottom: 20px;
    &::before {
      content: '';
      position: absolute;
      top: -53px;
      left: -89px;
      width: 20px;
      height: 20px;
      @include bg-cover;
      background-image: url('../img/explore-effect.png');
    }
  }
  &__descr {
    position: relative;
    margin: 0;
    margin-bottom: 33px;
    &::after {
      content: '';
      position: absolute;
      right: 103px;
      bottom: -26px;
      width: 20px;
      height: 20px;
      @include bg-cover;
      background-image: url('../img/explore-effect2.png');
    }
  }
}
